/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

#headerlogin {
    background: var(--bg-color);
    transition: all 0.5s;
    z-index: 997;
    padding: 38px 0;
    /* top: 40px; */
    box-shadow: 0px 5px 10px rgb(0 0 0 / 5%);
}

#headerlogin .header-scrolled {
    top: 0;
}

#headerlogin .logo {
    font-size: var(--h1-font-size);
    position:fixed;
    margin: 0;
    padding: 0;
    line-height: 1;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-family: var(--body-font);
}

#headerlogin .logo a {
    color: var(--text-color);
    text-decoration: none;
    
}

#headerlogin .logo img {
    max-height: 40px;
}

.fixed-top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
}

.headerlogin {
    display: block;
}


