/* Login Form Box */
.center {
    position: relative;
    /* top: 5%; */
    margin-top: 370px;
    left: 30%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 500px;
    border-radius: var(--border-radius-soft);
    box-shadow: var(--box-shadow-normal);
    font: var(--body-font);
    background: white;
    padding: 3%;
}

.center h1 {
    text-align: center;
    padding: 20px 0 10px 0;
    font-size: var(--h1-font-size);
    
}

.center form {
    padding: 0 90px;
    box-sizing: border-box;
}


#sub {
    text-align: center;
    color: var(--text-color-alt);
}

#titleform {
    text-align: center;
    color: var(--text-color-alt);
    padding-top:8%;
}

form .txt_field {
    position: relative;
    margin: 20px;
}

.txt_field input {
    width: 100%;
    padding: 5px 10px;
    height: 30px;
    font-size: var(--normal-font-size);
    border: unset;
    border-radius: 7px;
    box-shadow: rgb(0 0 0 / 0%) 0px 0px 0px 0px, rgb(0 0 0 / 0%) 0px 0px 0px 0px, rgb(0 0 0 / 0%) 0px 0px 0px 0px, rgb(60 66 87 / 16%) 0px 0px 0px 1px, rgb(0 0 0 / 0%) 0px 0px 0px 0px, rgb(0 0 0 / 0%) 0px 0px 0px 0px, rgb(0 0 0 / 0%) 0px 0px 0px 0px;
    background: rgb(255, 255, 255);
    outline-color: var(--text-color-alt);
}

.txt_field label {
    top: 50%;
    left: 5px;
    color: var(--text-color-alt);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: var(--normal-font-size);
    pointer-events: none;
    
}

.pass {
    margin: -23px 0 5px 195px;
    margin-bottom: 25px;
    color: #5D5FEF;
    cursor: pointer;
}

.pass:hover {
    text-decoration: underline;
}

input[type="submit"] {
    width: 100%;
    height: 45px;
    border: var(--border-edge);
    background: var(--secondary-color);
    border-radius: var(--border-radius-soft);
    color: var(--text-color-light);
    font-size: var(--normal-font-size);
    font-weight: var(--font-semi-bold);
}

input[type="submit"]:hover {
    background-color: var(--primary-color);
    transition: .5s;
}

.signup_link {
    margin: 30px 0;
    text-align: center;
    font-size: var(--normal-font-size);
    color: var(--text-color-alt);
}

.signup_link a {
    color: var(--primary-color);
    text-decoration: none;
    font-weight: var(--font-semi-bold);
}

.signup_link a:hover {
    text-decoration: underline;
    transition: .5s;
}

.field-checkbox input {
    width: 15px;
    height: 15px;
    margin-top:10px;
    margin-right: 10px; 
    box-shadow: unset;
    min-height: unset;
}
.field-checkbox label {
    display: flex;
    margin-top:10px;
    align-items: center;
    margin: 0;
}

.img_hero_login {
    position: relative;
    width: 733px;
    height: 757px;
    left: 60px;
    top: -10px;
    
  }

.img_hero_login img {
    width: 650px;
    height: 670px;
    margin: -20px 0 0 130px;
  }

 #herologin {
    padding: 0;
    margin: 0;
    /* min-height: calc (100vh - 984.6px - 986.4px); */
    min-height: 100%;
 } 


 section {
    padding: 0!important;
    margin: 0 !important;
 }


/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/

/*======= GOOGLE FONTS =======*/
:root {
    --header-height: 3rem;
    /*======= NAV BARS =======*/
    /*======= VARIABLES CSS =======*/
    /*---------- colors ----------*/
    --primary-color: #5d5fef;
    --secondary-color: #7879f1;
    --tertiary-color: #a5a6f6;
    --text-color: #25282b;
    --text-color-alt: #52575c;
    --text-color-light: #fff;
    --bg-color: #fafafa;
    --button-color-success: #064e3b;
    --button-color-info: #1e2a8a;
    --button-color-warning: #78350f;
    --button-color-error: #7f1d1d;
    --button-color-resched: #6c0986;
    /*----------font and typography ----------*/
    --body-font: "Montserrat", sans-serif;
    --biggest-font-size: 6rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --h4-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
    /*---------- font weight ----------*/
    --font-light: 400;
    --font-medium: 500;
    --font-semi-bold: 600;
    /*---------- margins ----------*/
    --mb-1: 0.5rem;
    --mb-2: 1rem;
    --mb-3: 1.5rem;
    --mb-4: 2rem;
    --mb-5: 2.5rem;
    --mb-6: 3rem;
    /*---------- base ----------*/
    --box-shadow-normal: 0px 13px 19px rgba(0, 0, 0, 0.07);
    --border-edge: 1px solid var(--primary-color);
    --border-radius-soft: 6px;
    /*======= RESPONSIVE =======*/
    @media screen and (min-width: 768px) {
         :root {
            --biggest-font-size: 6rem;
            --h1-font-size: 2.25rem;
            --h2-font-size: 1.5rem;
            --h3-font-size: 1.25rem;
            --normal-font-size: 1rem;
            --small-font-size: 0.875rem;
            --smaller-font-size: 0.813rem;
        }
    }
}


/*--------------------------------------------------------------
# STYLE
--------------------------------------------------------------*/

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-family: var(--body-font);
  color: #25282b;
  color: var(--text-color);
  background: #fafafa;
  background: var(--bg-color);
  min-height: 100vh;
  display: flex;
}

/* label,
span {
  font-weight: 500;
} */

body, html, .App, #root,.auth-wrapper {
    width: 100%;
    height: 100%;
    /* overflow: hidden; */
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  flex-direction:row;
  align-items: center;
}

.auth-inner {
  width: 500px;
  margin: auto;
  background: white;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 30px 55px 30px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: var(pri);
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #52575c;
  color: var(--text-color-alt);
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.forgot-password a:hover {
  cursor: pointer;
}

.btn-primary{
  /* background-color: var(--secondary-color); */
  --bs-btn-color: #fff;
  --bs-btn-bg: var(--secondary-color);
  --bs-btn-border-color: var(--primary-color);
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: var(--primary-color);
  --bs-btn-hover-border-color: var(--primary-color);
  --bs-btn-focus-shadow-rgb: 49,132,253;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: var(--primary-color);
  --bs-btn-active-border-color: var(--primary-color);
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: var(--primary-color);
  --bs-btn-disabled-border-color: var(--secondary-color);
}

.btn-outline-primary{
  --bs-btn-color: var(--secondary-color);
  --bs-btn-border-color: var(--secondary-color);
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: var(--primary-color);
  --bs-btn-hover-border-color: var(--primary-color);
  --bs-btn-focus-shadow-rgb: 13,110,253;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: var(--primary-color);
  --bs-btn-active-border-color: var(--primary-color);
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: var(--primary-color);
  --bs-btn-disabled-bg: transparent;
  --bs-btn-disabled-border-color: var(--secondary-color);
  --bs-gradient: none;
}

.image-banner {
  position: relative;
  width: 733.97px;
  height: 757px;
  left: 26px;
  top: -10px;
  
}

.image-banner img {
  width: 620px;
  height: 650px;
  margin: 10px 0 0 130px;
}

.label{
  font-weight: 600;
  color: rgba(1, 41, 112, 0.6);
}

